import { lazy, Suspense } from "react";
import { Route, BrowserRouter as Router, Routes } from "react-router-dom";
import MainStoreProvider from "store/context";
import "./App.scss";

const Home = lazy(() => import("./pages/Home"));
const Example = lazy(() => import("./pages/Examples"));
const TechnicalSkills = lazy(() => import("./pages/TechnicalSkills"));
const AttendedProjects = lazy(() => import("./pages/AttendedProjects"));

function App() {
  return (
    <MainStoreProvider>
      <Router>
        <Suspense fallback={<div>Loading...</div>}>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/skills" element={<TechnicalSkills />} />
            <Route path="/attended-projects" element={<AttendedProjects />} />
            <Route path="/demo" element={<Example />} />
          </Routes>
        </Suspense>
      </Router>
    </MainStoreProvider>
  );
}

export default App;
